.main-container {
  margin: 0 auto;
}

.hero {
  background-color: #cdd0d3;
  background-image: url("/img/hero@1x.jpg");
  background-image: image("/source/img/hero@1x.jpg#xywh=0,50,350,389");
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  height: 389px;
  margin: 0;
  padding: 0;
}

@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("/img/hero@2x.jpg");
  }
}

.hero__slogan-image {
  display: block;
  margin: 0 auto;
  padding-top: 90px;
}

.hero-for-catalog-form {
  background-color: #cdd0d3;
  background-image: url("/img/hero@1x.jpg");
  background-image: image("/source/img/hero@1x.jpg#xywh=0,50,350,389");
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  height: 389px;
  margin: 0;
  padding: 0;

  @media (min-resolution: 2dppx) {
    background-image: url("/img/hero@2x.jpg");
  }
}

// ОКНО УСПЕШНОЙ ОТПРАВКИ ФОРМЫ
.form__sent-message {
  display: none;
}

// ОКНО ОШИБКИ
.form__error-message {
  display: none;
}

// МОДАЛЬНЫЕ ОКНА
.modal {
  text-align: center;
  opacity: 100%;

  &::before {
    content: "";
    background: #000000;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0.3;
    z-index: 10;
  }
}

.modal__wrapper {
  background-color: rgba(255, 255, 255, 1);
  width: 100%;
  position: fixed;
  top: 25%;
  z-index: 11;
}

.modal__title {
  margin: 0;
  padding-top: 39px;
  font-size: 21px;
}

.modal__text {
  margin: 0;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 37px;
  padding-right: 39px;
}

.modal__button {
  font-size: 18px;
  padding-top: 18px;
  padding-bottom: 14px;
  padding-left: 60px;
  padding-right: 60px;
  margin: 20px;
  border: none;
}

.succses {
  .button-wrapper {
    background-color: rgba(242, 242, 242, 1);
  }
}

.error {
  .modal__button {
    margin-top: 0;
  }
}

